import { Link } from "react-router-dom";
import styled from "styled-components";

export const Header = () => {
  return (
    <SHeader>
      <SLink to="/">HOME</SLink>
      <SLink to="/users">USERS-PAGE</SLink>
    </SHeader>
  );
};

const SHeader = styled.header`
  background-color: #24699e;
  color: #ffff;
  text-align: center;
  padding: 8px 0;
`;

const SLink = styled(Link)`
  margin: 0 8px;
  color: #fff;
`;
